import React from 'react';
import './index.css';
import OneTeamMember from '../Items/OneTeamMember';

const TeamMember = (props) => {
    return (
        <div className='team_members container'>
            <div className='team_members__text text-center mx-auto'>
                <h4 className="mt-4 text-uppercase text-success">Team Chng Ti</h4>
                <h2>Gp g thnh vin ca SynerLink</h2>
                <p className='mt-4'>Chng ti l SynerLink - mt nn tng kt ni v h tr cng ng vi s mnh to ra nhng thay i tch cc trong x hi. Chng ti tin rng mi c m u ng qu v mi ngi u c kh nng gp phn vo vic thc hin chng</p>
            </div>
            <div className="row">
                {props.team.map((teamMember, index) =>
                    <OneTeamMember key={index} teamMember={teamMember} />
                )}
            </div>
        </div>
    );
}

export default TeamMember;
